import React from "react";
import "./formInfo.css";
import { useNavigate } from "react-router-dom";

const Details = () => {
  const letsGo = useNavigate();
  const moveToRegister = () => {
    letsGo("/register");
  };
  return (
    <div className="Back">
      <div className="reg1">
        <div className="row">
          <div className="col-md-3 reg1-left">
            <h1 className="vecto-header">VectoGames Registration </h1>
          </div>
          <div className="col-md-9 reg1-right">
            <h3 className="reg-heading">Events Detail</h3>
            <div className="row reg1-form">
              <p>
                Event Date: May 22nd, 2022<br></br>
                Event Time: 9 am to 6 pm<br></br>
                Event Address: Sant Dnyaneshwar Sports Complex, Bhosari, Pune.
                <br></br>
                Contact us at contact@vectog.com or
                https://www.instagram.com/vectog/ or<br></br>
                What's app - +91 8484925888{" "}
              </p>
              <p>
                {" "}
                1. An email address is required to start the process.<br></br>
                2. All the fields in the registration form must be filled,
                unless otherwise noted.<br></br>
                3. Only athletes living in the state of Maharashtra will be
                allowed to register.<br></br>
                4. The athlete needs to be at least 16 years at the time of
                competition to be eligible to participate.<br></br>
                5. The Athletes under the age of 18 years can only participate
                in one event above the race distance of 200m.<br></br>
                6. Only the forms and fees submitted through the website will be
                accepted.<br></br>
                7. One athlete can only participate in two individual and two
                mixed relay events.<br></br>
                8. The registration will be open from April 13 to May 15, 2022.
                <br></br>
                9. Any entries that are wrongly filled will be sent back or
                declared ineligible.<br></br>
                10. The VectoG performance reserves the right to decide the
                outcomes and any other official decision made during the
                VectoGames 2022. <br></br>
              </p>
              Club/organizations combined athlete registrations, please contact
              us to get the registration sheet.
              <button
                onClick={moveToRegister}
                type="submit"
                className="register-btn"
                value=""
              >
                Fill out form
              </button>
            </div>
          </div>
        </div>
      </div>
      <div></div>
    </div>
  );
};
export default Details;
