import axios from "axios";
import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import "./register.css";

const Personal = () => {
  const letsGo = useNavigate();

  const [FirstName, setFirstName] = useState("");

  const [LastName, setLastName] = useState("");
  const [DateofBirth, setDateofBirth] = useState("");
  const [FathersName, setFathersName] = useState("");
  const [MothersName, setMothersName] = useState("");
  const [Email, setEmail] = useState("");
  const [PhoneNo, setPhoneNo] = useState("");
  const [Country, setCountry] = useState("");
  const [Address, setAddress] = useState("");
  const [ZipCode, setZipCode] = useState("");
  const [Organisation, setOrganisation] = useState("");
  const [GuardiansPhoneNo, setGuardiansPhoneNo] = useState("");
  const [event1, setEvent1] = useState("");
  const [event2, setEvent2] = useState("");
  const [e1pf, setE1pf] = useState("");
  const [e2pf, setE2pf] = useState("");
  const [m1, setM1] = useState("");
  const [m2, setM2] = useState("");
  const [sign, setSign] = useState("");
  const [liability, setLiability] = useState("");
  const [sum, setSum] = useState(0);

  const submit = () => {
    axios
      .post("http://localhost:5000/userRegister", {
        FirstName: FirstName,
        LastName: LastName,
        DateofBirth: DateofBirth,
        FathersName: FathersName,
        MothersName: MothersName,
        Email: Email,
        PhoneNo: PhoneNo,
        Country: Country,
        Address: Address,
        ZipCode: ZipCode,
        Organisation: Organisation,
        GuardiansPhoneNo: GuardiansPhoneNo,
        event1: event1,
        event2: event2,
        e1pf: e1pf,
        e2pf: e2pf,
        m1: m1,
        m2: m2,
        sign: sign,
        liability: liability,
        amount: sum,
      })
      .then((result) => {
        setAddress("");
        setCountry("");
        setDateofBirth("");
        setEmail("");
        setFathersName("");
        setFirstName("");
        setGuardiansPhoneNo("");
        setLastName("");
        setMothersName("");
        setOrganisation("");
        setZipCode("");
        setPhoneNo("");
        setE1pf("");
        setE2pf("");
        setEvent1("");
        setEvent2("");
        setM1("");
        setM2("");
        setLiability(false);
        setSign("");
        setSum();
        localStorage.setItem("id", result?.data?.results?.insertId);
        letsGo("/userdata");
      });
  };

  return (
    <div
      style={{
        background: "-webkit-linear-gradient(left, #3931af, #00c6ff)",
        marginBottom: "0%",
      }}
    >
      {/* <form> */}
      <div className="register ">
        <div className="row">
          <div className="col-md-3 register-left">
            <h1 className="vecto-header">VectoGames </h1> <br></br>
            {/* <br></br> */}
            <h1 className="header-reg">Registration </h1>
            <br></br>
          </div>

          <div className="col-md-9 register-right">
            <div className="tab-content" id="myTabContent">
              <div
                className="tab-pane fade show active"
                id="home"
                role="tabpanel"
                aria-labelledby="home-tab"
              >
                <h2 className="register-headi">Personal Information </h2>

                <div className="row register-form">
                  <div className="col-md-6">
                    <div className="form-group ">
                      <input
                        required
                        type="text"
                        className="form-control "
                        name=" FirstName"
                        placeholder="First Name *"
                        value={FirstName}
                        onChange={(e) => setFirstName(e.target.value)}
                      />
                    </div>
                    <br></br>

                    <div className="form-group">
                      <input
                        required
                        type="text"
                        name=" LastName"
                        className="form-control"
                        placeholder=" Last Name *"
                        value={LastName}
                        onChange={(e) => setLastName(e.target.value)}
                      />
                    </div>
                    <br></br>
                    <div className="form-group">
                      <input
                        required
                        style={{ color: "grey" }}
                        type="date"
                        className="form-control"
                        name="DateOfBirth"
                        placeholder="Date of Birth"
                        value={DateofBirth}
                        max="2006-12-31"
                        onChange={(e) => setDateofBirth(e.target.value)}
                      />
                    </div>
                    <br></br>
                    <div className="form-group">
                      <input
                        required
                        type="text"
                        name="FathersName "
                        placeholder="Father's Name *"
                        className="form-control"
                        value={FathersName}
                        onChange={(e) => setFathersName(e.target.value)}
                      />
                    </div>
                    <br></br>
                    <div className="form-group">
                      <input
                        required
                        type="text"
                        name="MothersName "
                        placeholder="Mother's Name *"
                        className="form-control"
                        value={MothersName}
                        onChange={(e) => setMothersName(e.target.value)}
                      />
                    </div>
                    <br></br>
                    <div className="form-group">
                      <input
                        required
                        type="text"
                        name="GuardiansPhoneNo"
                        className="form-control"
                        placeholder="Guardians Phone No. *"
                        value={GuardiansPhoneNo}
                        onChange={(e) => setGuardiansPhoneNo(e.target.value)}
                      />
                    </div>
                  </div>
                  <div className="col-md-6">
                    <div className="form-group">
                      <input
                        required
                        type="text"
                        className="form-control "
                        name="Email"
                        placeholder="Email *"
                        value={Email}
                        onChange={(e) => setEmail(e.target.value)}
                      />
                    </div>
                    <br></br>

                    <div className="form-group">
                      <input
                        required
                        type="text"
                        name="PhoneNo"
                        className="form-control"
                        placeholder="Phone No. *"
                        value={PhoneNo}
                        onChange={(e) => setPhoneNo(e.target.value)}
                      />
                    </div>
                    <br></br>
                    <div className="form-group">
                      <input
                        required
                        type="text"
                        name="Country "
                        className="form-control"
                        placeholder="Country *"
                        value={Country}
                        onChange={(e) => setCountry(e.target.value)}
                      />
                    </div>
                    <br></br>
                    <div className="form-group">
                      <input
                        required
                        type="text"
                        className="form-control"
                        name="Address"
                        placeholder="Address *"
                        value={Address}
                        onChange={(e) => setAddress(e.target.value)}
                      />
                    </div>
                    <br></br>
                    <div className="form-group">
                      <input
                        required
                        type="text"
                        className="form-control"
                        name=" ZipCode"
                        placeholder="Zipcode *"
                        value={ZipCode}
                        onChange={(e) => setZipCode(e.target.value)}
                      />
                    </div>
                    <br></br>
                    <div className="form-group">
                      <input
                        required
                        type="text"
                        className="form-control"
                        name="Organisation"
                        placeholder="Organisation *"
                        value={Organisation}
                        onChange={(e) => setOrganisation(e.target.value)}
                      />
                    </div>
                  </div>
                </div>

                <h2 className="register-headi" style={{ marginTop: "0" }}>
                  Event Participation{" "}
                </h2>
                <div
                  className="row register-form"
                  style={{ paddingBottom: "40px" }}
                >
                  <div style={{ fontSize: "12px" }}>
                    <p>
                      - Registration cost:  250 per individual event and  400
                      per mixed relay
                    </p>
                    <p>- Maximum of 2 individual events per athlete</p>
                  </div>
                  <div className="col-md-6">
                    <div className="form-group">
                      <select
                        style={{ color: "grey" }}
                        className="form-control"
                        onChange={(e) => {
                          setM1(e.target.value);
                          setSum(sum + 400);
                        }}
                      >
                        <option className="hidden" selected disabled>
                          {" "}
                          Select Mixed Relay Event 1
                        </option>
                        <option value=" 4 X 100 meter mixed relay">
                          4 X 100 meter mixed relay
                        </option>
                        <option value=" 4 X 400 meter mixed relay">
                          4 X 400 meter mixed relay
                        </option>
                      </select>
                    </div>
                    <br />

                    <div className="form-group">
                      <select
                        style={{ color: "grey" }}
                        className="form-control"
                        required
                        onChange={(e) => {
                          setEvent1(e.target.value);
                          setSum(sum + 250);
                        }}
                      >
                        <option className="hidden" selected disabled>
                          {" "}
                          Event 1
                        </option>
                        <option value="100 meter">100 meter</option>
                        <option value="200 meter">200 meter</option>
                        <option value="400 meter">400 meter </option>

                        <option value="800 meter">800 meter</option>
                        <option value="1500 meter">1500 meter</option>
                        <option value="3000 meter">3000 meter (women)</option>
                        <option value="5000 meter">5000 meter (men)</option>
                        <option value="100 meter hurdle (women only)">
                          100 meter hurdle (women)
                        </option>
                        <option value="110 meter hurdle (men only)">
                          110 meter hurdle (men)
                        </option>
                        <option value="400 meter hurdle women">
                          400 meter hurdle (women)
                        </option>
                        <option value="400 meter hurdle men">
                          {" "}
                          400 meter hurdle (men)
                        </option>
                        <option value="Long Jump">Long Jump</option>
                        <option value="Triple Jump"> Triple Jump</option>
                        <option value="High Jump"> High Jump</option>
                        <option value="Shot Put">Shot Put</option>
                        <option value="Discuss Throw">Discuss Throw</option>
                        <option value="Hammer Throw">Hammer Throw</option>
                        <option value="Javelin">Javelin</option>
                      </select>
                    </div>
                    <br></br>

                    <div className="form-group">
                      <input
                        type="text"
                        name="e1pf "
                        placeholder="Event 1 Performance"
                        className="form-control"
                        value={e1pf}
                        onChange={(e) => setE1pf(e.target.value)}
                      />
                    </div>
                    <br></br>
                    <div className="form-group"></div>
                  </div>
                  <div className="col-md-6">
                    <div className="form-group">
                      <select
                        style={{ color: "grey" }}
                        className="form-control"
                        // value={m2}
                        onChange={(e) => {
                          setM2(e.target.value);
                          setSum(sum + 400);
                        }}
                      >
                        <option className="hidden" selected disabled>
                          {" "}
                          Select Mixed Relay Event 2
                        </option>
                        <option value=" Mixed 4 X 100 meter relay">
                          Mixed 4 X 100 meter relay
                        </option>
                        <option value=" Mixed 4 X 400 meter relay">
                          Mixed 4 X 400 meter relay
                        </option>
                      </select>
                    </div>

                    <br></br>
                    <div className="form-group">
                      <select
                        style={{ color: "grey" }}
                        className="form-control"
                        required
                        onChange={(e) => {
                          setEvent2(e.target.value);
                          setSum(sum + 250);
                        }}
                      >
                        <option className="hidden" selected disabled>
                          {" "}
                          Event 2
                        </option>
                        <option value="100 meter">100 meter</option>
                        <option value="200 meter">200 meter</option>
                        <option value="400 meter">400 meter </option>

                        <option value="800 meter">800 meter</option>
                        <option value="1500 meter">1500 meter</option>
                        <option value="3000 meter">3000 meter (women)</option>
                        <option value="5000 meter">5000 meter (men)</option>
                        <option value="100 meter hurdle (women only)">
                          100 meter hurdle (women)
                        </option>
                        <option value="110 meter hurdle (men only)">
                          110 meter hurdle (men)
                        </option>
                        <option value="400 meter hurdle women">
                          400 meter hurdle (women)
                        </option>
                        <option value="400 meter hurdle men">
                          {" "}
                          400 meter hurdle (men)
                        </option>
                        <option value="Long Jump">Long Jump</option>
                        <option value="Triple Jump"> Triple Jump</option>
                        <option value="High Jump"> High Jump</option>
                        <option value="Shot Put">Shot Put</option>
                        <option value="Discuss Throw">Discuss Throw</option>
                        <option value="Hammer Throw">Hammer Throw</option>
                        <option value="Javelin">Javelin</option>
                      </select>
                    </div>
                    <br></br>
                    <div className="form-group">
                      <input
                        type="text"
                        name="e2pf"
                        className="form-control"
                        placeholder="Event 2 Performance"
                        value={e2pf}
                        onChange={(e) => setE2pf(e.target.value)}
                      />
                    </div>

                    <br></br>
                    <div className="form-group"></div>
                  </div>
                  <hr></hr>
                  <div style={{ fontSize: "12px" }}>
                    <label>
                      <b>
                        Liability Waiver{" "}
                        <span className="liability-color">*</span>
                      </b>
                    </label>

                    <p className="para-participation">
                      {" "}
                      <input
                        className="checkbox"
                        type="checkbox"
                        name="liability"
                        value={liability}
                        onChange={(e) => setLiability(e.target.checked)}
                        required
                      />{" "}
                      <span> &nbsp;</span>
                      I, the undersigned, being aware of my own health and
                      physical condition, and having knowledge that my
                      participation in any competition may be injurious to my
                      health, am voluntarily participating in a physical
                      activity. Having such knowledge, I hereby acknowledge this
                      release, the organizers of VectoGames, any
                      representatives, agents, and successors from liability for
                      accidental injury or illness which I may incur as a result
                      of participating in the said competition. I hereby assume
                      all risks connected therewith and consent to participate
                      in said competition. I agree to disclose any physical
                      limitations, disabilities, ailments, or impairments which
                      may affect my ability to participate in said fitness
                      program.
                    </p>
                    <label>
                      <b>
                        Sign <span className="liability-color">*</span> :{" "}
                      </b>
                      <input
                        className="sign"
                        type="text"
                        name="sign"
                        placeholder="Enter your Full Name..."
                        value={sign}
                        onChange={(e) => setSign(e.target.value)}
                        required
                      />
                    </label>
                    <br />
                    <label htmlFor="">
                      <b>
                        {" "}
                        Total Amount : INR <span> </span>
                      </b>
                    </label>
                    <input
                      className="amount"
                      type="number"
                      placeholder={sum}
                      value={sum}
                      disabled={true}
                      onChange={(e) => setSum(e.target.value)}
                    />

                    <button
                      className="btnRegister"
                      onClick={submit}
                      style={{ width: "30%" }}
                      disabled={
                        !Email ||
                        !LastName ||
                        !FirstName ||
                        !PhoneNo ||
                        !MothersName ||
                        !FathersName ||
                        !DateofBirth ||
                        !GuardiansPhoneNo ||
                        !ZipCode ||
                        !Country ||
                        !Address ||
                        !Organisation ||
                        !liability ||
                        !sign
                      }
                    >
                      Submit
                    </button>

                    <br />
                  </div>
                </div>
                {/* </div> */}
              </div>
            </div>
          </div>
        </div>
      </div>
      {/* </form> */}
    </div>
  );
};

export default Personal;
