import React, { useState, useEffect } from "react";
import "./userInfo.css";
import axios from "axios";
import { useNavigate } from "react-router-dom";

const Events = () => {
  const letsGo = useNavigate();
  const [user, setUser] = useState({});
  const [ord, setOrders] = useState();
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    getUserInfo();
  }, []);

  const getUserInfo = () => {
    axios
      .get(`http://localhost:5000/user/${localStorage.getItem("id")}`)
      .then((x) => {
        setUser(x?.data?.response[0]);
      });
  };

  const checkout = () => {
    axios
      .post("http://localhost:5000/order", {
        amount: user.amount,
      })
      .then((results) => {
        setOrders(results?.data?.sub?.id);
      });
    const script = document.createElement("script");
    script.src = "https://checkout.razorpay.com/v1/checkout.js";
    script.onerror = () => {
      alert("Razorpay SDK failed to load. Are you online?");
    };
    script.onload = async () => {
      try {
        setLoading(true);
        const result = await axios.post("http://localhost:5000/order", {
          amount: user?.amount * 100,
        });
        const { id } = result?.data?.sub;
        const {
          data: { key: razorpayKey },
        } = await axios.get("http://localhost:5000/get-razorpay-key");

        const options = {
          key: razorpayKey,
          amount: user?.amount * 100,
          currency: "INR",
          name: user?.FirstName + " " + user?.LastName,
          description: "VectoG transaction",
          order_id: id,
          payment_capture: "1",
          handler: async function (response) {
            const result = await axios.post(
              `http://localhost:5000/pay-order/${localStorage.getItem("id")}`,
              {
                amount: user?.amount,
                razorpayPaymentId: response?.razorpay_payment_id,
                razorpayOrderId: response?.razorpay_order_id,
                razorpaySignature: response?.razorpay_signature,
              }
            );

            letsGo("/thankyou");
          },
          prefill: {
            name: user?.FirstName + " " + user?.LastName,
            email: user?.Email,
            contact: user?.PhoneNo,
          },
          theme: {
            color: "#80c0f0",
          },
        };

        setLoading(false);
        const paymentObject = new window.Razorpay(options);
        paymentObject.open();
      } catch (err) {
        alert(err);
        setLoading(false);
      }
    };
    document.body.appendChild(script);
  };

  return (
    <div
      style={{
        background: "-webkit-linear-gradient(left, #3931af, #00c6ff)",
        marginBottom: "0%",
        paddingBottom: "35%",
      }}
    >
      <div className=" register">
        <div className="row">
          <div className="col-md-3 register-left">
            <h1 className="vecto-header">
              VectoGames <br />
              <span style={{ marginTop: "15px" }}>Registration</span>{" "}
            </h1>
          </div>
          <div className="col-md-7 register-right">
            <ul
              className="nav nav-tabs nav-justified"
              id="myTab"
              role="tablist"
            ></ul>
            <div className="tab-content" id="myTabContent">
              <div
                className="tab-pane fade show active"
                id="home"
                role="tabpanel"
                aria-labelledby="home-tab"
              >
                <h2 className="register-head">User Information</h2>
                <br />
                <div className="row register-form">
                  <h5 name="FirstName" value="FirstName">
                    <b> First Name :</b> {user?.FirstName}{" "}
                  </h5>
                  <h5 name="LastName" value="LastName">
                    <b> Last Name :</b> {user?.LastName}{" "}
                  </h5>
                  <h5 name="PhoneNo" value="PhoneNo">
                    <b> Phone Number :</b> {user?.PhoneNo}{" "}
                  </h5>
                  <h5 name="Email" value="Email">
                    <b> Email :</b> {user?.Email}{" "}
                  </h5>
                  <h5 name="Amount" value="Amount">
                    {" "}
                    <b> Total Amount :</b> {user?.amount}{" "}
                  </h5>

                  <button
                    type="submit"
                    className="btnRegister"
                    value=""
                    onClick={checkout}
                    style={{ alignItems: "right" }}
                  >
                    Pay Now
                  </button>
                </div>
              </div>
            </div>
          </div>
          <div></div>
        </div>
      </div>
    </div>
  );
};

export default Events;
